
import draggable from "vuedraggable";
import { onMounted, ref } from "vue";
import { CIcon } from "@coreui/icons-vue";
import {
  cilElevator,
  cilChevronBottom,
  cilChevronTop,
  cilX,
} from "@coreui/icons";
import Slider from "../components/Slider.vue";

export default {
  name: "clone",
  display: "Clone",
  order: 2,
  components: {
    draggable,
    CIcon,
    Slider,
  },

  data() {
    return {
      value: 'Header'
    }
  },

  setup() {
    const menuOpen = ref(null);
    const list1 = ref([]);
    const list2 = ref([]);
    const open = ref(false);
    const element = ref("");
    const currentIndex = ref(null);

    const toggleMenu = function () {
      if (menuOpen.value === null) {
        menuOpen.value = true;
      } else {
        menuOpen.value = null;
      }
      console.log(menuOpen.value);
    };

    const setActive = function(e) {
      const HTMLelements = document.querySelector("#drag").children;
      const index = Array.prototype.indexOf.call(HTMLelements, e.currentTarget)
      currentIndex.value = index;
    }

    const sliderClose = function () {
      open.value = false;
      console.log(currentIndex.value)
      const compHTML = document.querySelector("#drag").children[currentIndex.value].lastElementChild.firstElementChild.outerHTML;
      list2.value[currentIndex.value].components = compHTML;
    }

    const generateID = function () {
      let ID = 0;
      list2.value.forEach((element) => {
        element.id = ID;
        ID++;
        // console.log(element.id);
      });
      setTimeout(1000, log());
    };

    const removeItem = function (id) {
      const newList = list2.value.filter((element) => {
        if (element.id !== id) {
          return element;
        }
        console.log(element.id);
      });
      list2.value = newList;
    };

    const log = function () {
        document.querySelectorAll("[editable-element]").forEach((el) => {
          el.addEventListener("click", (e) => {
            open.value = true;
            element.value = e.target;
           });
        })  
      }
    

    const getFile = function () {
      let urls = ["1.html", "2.html"];
      let descriptions = ["Subscribe to newsletter", "Our offices"];

      try {
        urls.forEach(async (url, i) => {
          const response = await fetch("html/" + url);
          const data = await response.text();
          list1.value.push({
            name: i + 1,
            description: descriptions[i],
            components: data,
          });
        });
      } catch (err) {
        console.log("error");
      }
    };

    const cloneElement = function (original) {
      const element = { ...original };
      return element;
    };

    onMounted(() => {
      getFile();
    });

    return {
      list1,
      list2,
      cilElevator,
      cilChevronBottom,
      cilChevronTop,
      cilX,
      menuOpen,
      toggleMenu,
      Slider,
      open,
      removeItem,
      generateID,
      cloneElement,
      element,
      log,
      sliderClose,
      setActive,

    };
  },
  methods: {
    
    }


};
