
import {
  Dialog,
  DialogOverlay,
  DialogTitle,
  TransitionChild,
  TransitionRoot,
} from "@headlessui/vue";
import { XIcon } from "@heroicons/vue/outline";
export default {
  components: {
    Dialog,
    DialogOverlay,
    DialogTitle,
    TransitionChild,
    TransitionRoot,
    XIcon,
  },
  props: {
    open: {
      type: Boolean,
      required: true,
    },
    element: {
      required: true,
    },
  },
  setup(props) {
    console.log(props.element);

    const changeColor = function () {
      console.log(props.element);
      let currentColor = document.querySelector("#text-color").value;
      props.element.style.color = currentColor;
    }

    const changeBgColor = function () {
      console.log(props.element);
      let currentBgColor = document.querySelector("#bg-color").value;
      props.element.style.backgroundColor = currentBgColor;
    }

    return {
      changeColor,
      changeBgColor
    };


  },
};
